import { Component } from '@angular/core';
import { IonicPage,ModalController, NavController, NavParams } from 'ionic-angular';
import { Contacts} from '../../providers';
import { SocialSharing } from '@ionic-native/social-sharing';
import { CallNumber } from '@ionic-native/call-number';


@IonicPage()
@Component({
  selector: 'page-lead',
  templateUrl: 'lead.html',
})
export class LeadPage {
  items : any ;
  btnCall = false;
  btnEmail = false;
   seccion     :string   = "datos";
  constructor(private socialSharing: SocialSharing, private callNumber: CallNumber,public modalCtrl: ModalController,public contacts : Contacts ,public navCtrl: NavController, public navParams: NavParams) {
    ////console.log("lead");
  }

  ionViewDidLoad() {
  }


  call(number, fullname) {

    if (this.btnCall)
      return;
    this.btnCall = true;

    this.callNumber.callNumber(number, true)
      .then(res => this.btnCall = false)
      .catch(err => this.btnCall = false);

  }

  mail(email, fullname) {

    if (this.btnEmail)
      return;
    this.btnEmail = true;

    this.socialSharing.shareViaEmail('', 'Asunto', [email]).then(() => {

      this.btnEmail = false;
    }).catch(() => {

      this.btnEmail = false;
    });

  }

  doRefresh(event){

  }

  addComment() {

    let addModal = this.modalCtrl.create('AddcommentPage');
    addModal.onDidDismiss(item => {

    })
    addModal.present();

  }
  cita() {

    let addModal = this.modalCtrl.create('AddschedulePage');
    addModal.onDidDismiss(item => {

    })
    addModal.present();

  }
}



